.addInfoWrapper {
  padding: 10px;
}

.solidLine {
  width: 100%;
  height: 2px;
  background-color: #533b54;
  margin: 20px 0;
}

.listAdd {
  padding: 8px 0;
  display: flex;
  gap: 16px;
}

.linkAdd {
  font-size: 20px;
  font-weight: 500;
  color: #533b54;
}

.active {
  color: #5e0655;
  text-decoration: underline;
}

.linkAdd:hover,
.linkAdd:focus {
  color: #5e0655;
}
